import React, {useState, useEffect} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import {RFID_API_URL, API_URL} from '../../config';
import {Table,Card} from 'react-bootstrap';


import HotelNavbar from './HotelNavbar';

export default function AlbaranDetails(props) {
    let id = props.match.params.id;

    const [loggedInUser, setLoggedInUser] = useState (null);
    const [albaran, setAlbaran] = useState(null);
    const [complexData, setComplexData] = useState(null);
    const [albaranRepresentado, setAlbaranRepresentado] = useState(null);
    const [redirecting,setRedirecting] = useState (false);

    useEffect(()=>{
        axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggedInUser(result.data)
          console.log(result.data.rfidComplexId)
          axios.get(`${API_URL}/complex/${result.data.rfidComplexId}/details/byrfidid`,{withCredentials: true})
            .then((response)=>{
                console.log(response.data[0])
                setComplexData(response.data[0])
            })
        })
        .catch(() => {
            setRedirecting(true)
        })
        if(props){
            axios.get(`${API_URL}/customer/envio/${id}`,{withCredentials: true})
            .then((response)=>{
              setAlbaran(response.data);
            })
        }
    },[])

    if(redirecting) return <Redirect to={'/'}/>
    if(!albaran || !complexData) return <p>Loading...</p>
    // albaran.forEach(elem => {
    //     let articulo;
    //     let cantidad;
    //     axios.put(`https://smartlinen.loca.lt/sqlquery`,{sqlquery: `select * from articulos where id=${elem.articulo_id}`})
    //     .then((resArticle)=>{
    //         articulo = resArticle.data;
    //         axios.put(`https://smartlinen.loca.lt/sqlquery`,{sqlquery: `select count(serials_id) from serials_lineaenvio where lineaenvio1_id=${elem.id}`})
    //         .then((resQuantity)=>{
    //             cantidad = resQuantity.data;
    //             if(!albaranRepresentado) setAlbaranRepresentado([{articulo, cantidad}]);
    //             else {albaranRepresentado.push({articulo, cantidad})}
    //         })
    //     })
    // });
    //if(!albaranRepresentado)return <p>Loading...</p>
    //console.log(albaranRepresentado)
    let total = 0;
    return (
        <>
            <HotelNavbar loggedInUser={loggedInUser} logOut = {props.logOut}/>
            <Card style={{margin:'10px'}}>
            <Card.Header as="h5">Fecha: {albaran.envio[0].fecha.slice(0,10)} - {albaran.envio[0].fecha.slice(11,19)}</Card.Header>
            <Table striped bordered hover style={{}} id='ComplexLaundriesTable'>
                        <thead>
                            <tr>
                            <th>Artículo</th>
                            <th>Cantidad</th>
                            <th>Precio</th>
                            <th>Subtotal</th>
                            </tr>
                        </thead>
            {
                albaran? (albaran.serial.map((elem,i)=>{
                    console.log(elem)
                    let line = complexData.linen.find((linen)=>{
                        console.log(linen.laundry.rfidid, 'and', elem.articulo.rfidid)
                        return linen.laundry.rfidid === elem.articulo.rfidid
                    });
                    if(line) total += line.price * elem.cantidad;
                    return(
                        <tbody key={i+'alb_art'}>
                            <tr>
                                <td>{elem.articulo.name}</td>
                                <td>{elem.cantidad}</td>
                                <td>
                                {line? line.price: ''}
                                </td>
                                <td>{line? (line.price * elem.cantidad).toFixed(2) +' €': ''}</td>
                            </tr>
                        </tbody>
                        
                    )
                })):''
            }
            </Table>
            <h5 style={{textAlign:'center'}}>Total: {total.toFixed(2)}€</h5>
            </Card>
        </>
    )
}
